<template>
    <section class="common-dynamic-card-property">
        <el-tabs v-model="activeTab" stretch>
            <el-tab-pane label="" name="top">
                <el-form label-position="left">
                    <el-row :gutter="20">
                        <el-col :span="8">
                            <single-image-picker text="" :uploadedImage="property.smallCover"
                                                 @deleteSuccess="imageChange('smallCover',$event)"
                                                 @uploadSuccess="imageChange('smallCover',$event)"></single-image-picker>
                        </el-col>
                        <el-col :span="16">
                            <el-form-item label="" label-width="40px">
                                <el-input v-model="property.title" @input="propertyChange"></el-input>
                            </el-form-item>
                            <el-row :gutter="20">
                                <el-col :span="8">
                                    <el-form-item label="">
                                        <theme-color-picker v-model="property.titleColor"
                                                            @change="propertyChange"></theme-color-picker>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="8">
                                    <el-form-item label="">
                                        <el-switch v-model="property.smallCoverRound"
                                                   @change="propertyChange"></el-switch>
                                    </el-form-item>
                                </el-col>
                            </el-row>

                        </el-col>
                    </el-row>
                    <el-row :gutter="20">
                        <el-col :span="18">
                            <el-form-item label="" label-width="80px">
                                <el-input v-model="property.leftText" @input="propertyChange"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="">
                                <theme-color-picker v-model="property.leftTextColor"
                                                    @change="propertyChange"></theme-color-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="20">
                        <el-col :span="18">
                            <el-form-item label="" label-width="80px">
                                <el-input v-model="property.rightText" @input="propertyChange"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="">
                                <theme-color-picker v-model="property.rightTextColor"
                                                    @change="propertyChange"></theme-color-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="" name="text">
                <el-form>
                    <el-form-item>
                        <el-input type="textarea" placeholder="200" rows="8" maxlength="200" show-word-limit
                                  v-model="property.contentText" @input="propertyChange"></el-input>
                    </el-form-item>
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="" name="images">
                <el-alert
                        title="!  9 "
                        type="info">
                </el-alert>
                <mutil-image-picker style="margin-top:12px" v-model="property.images" @change="propertyChange"></mutil-image-picker>
            </el-tab-pane>
            <el-tab-pane label="" name="bottom">
                <el-alert
                        title="!   "
                        type="info">
                </el-alert>
                <icon-text-value-component style="margin-top: 8px" v-model="property.bottoms" @change="propertyChange"></icon-text-value-component>
            </el-tab-pane>
        </el-tabs>
    </section>
</template>

<script>
    export default {
        name: "common-dynamic-card-property",
        props: {
            property: {
                type: Object
            }
        },
        data() {
            return {
                activeTab: 'top'
            }
        },
        methods: {
            imagesChange(index, e) {
                this.$set(this.property.images, index, e)
                this.propertyChange()
            },
            imageChange(type, e) {
                if (type === 'smallCover') {
                    this.property.smallCover = e;
                }
                this.propertyChange()
            },
            propertyChange() {
                this.$emit('propertyChange', {
                    name: this.$options.name,
                    data: this.property
                })
            }
        }
    }
</script>

<style scoped>

</style>
