<template>
    <div class="common-single-button-property">
        <el-divider></el-divider>
        <el-form label-position="left">
            <el-row :gutter="10">
                <el-col :span="6">
                    <el-form-item label="" label-width="20px">
                        <el-input type="number" v-model="property.paddingTop" @input="onPropertyChange"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="" label-width="20px">
                        <el-input type="number" v-model="property.paddingRight" @input="onPropertyChange"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="" label-width="20px">
                        <el-input type="number" v-model="property.paddingBottom" @input="onPropertyChange"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="" label-width="20px">
                        <el-input type="number" v-model="property.paddingLeft" @input="onPropertyChange"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col :span="10">
                    <el-form-item label="">
                        <el-switch v-model="property.block" @change="onPropertyChange"></el-switch>
                    </el-form-item>
                </el-col>
                <el-col :span="14" v-show="!property.block">
                    <el-form-item label="">
                        <el-radio-group v-model="property.align" @change="onPropertyChange">
                            <el-radio label="left"></el-radio>
                            <el-radio label="center"></el-radio>
                            <el-radio label="right"></el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>

        </el-form>
        <common-button-property-component :property="property"
                                          @propertyChange="onPropertyChange"></common-button-property-component>
    </div>
</template>

<script>
    export default {
        name: "common-single-button-property",
        props: {
            property: {
                type: Object
            }
        },
        methods: {
            onPropertyChange() {
                this.$emit('propertyChange', {
                    name: this.$options.name,
                    data: this.property
                })
            }
        }
    }
</script>

