<template>
    <div class="click-event-manager">
        <el-divider v-if="title">{{title}}</el-divider>
        <el-form>
            <el-form-item label="">
                <el-radio-group v-model="config.type" @change="propertyChange">
                    <el-radio :label="0"></el-radio>
                    <el-radio :label="1"></el-radio>
                    <el-radio :label="2"></el-radio>
                    <el-radio :label="3"></el-radio>
                </el-radio-group>
            </el-form-item>
            <div class="nav-config" v-show="config.type==0">
                <el-alert
                        title="!"
                        type="info"
                        description=" []  []">
                </el-alert>
                <el-form-item label="">
                    <el-input type="text" v-model="config.navigationConfig.page"
                              placeholder="app,:/pages/a/b"
                              @input="propertyChange"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="config.navigationConfig.type" @change="propertyChange">
                        <el-radio :label="0"></el-radio>
                        <el-radio :label="1"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="text" v-model="config.navigationConfig.url"
                              placeholder=",: http://xxx.com"
                              @input="propertyChange"></el-input>
                </el-form-item>
            </div>
            <div class="toast-config" v-show="config.type==1">
                <el-alert
                        title="!"
                        type="info">
                    1. <br>2. <br>3. ,1=1000
                </el-alert>
                <el-form-item label="">
                    <el-input type="text" v-model="config.toastConfig.title" placeholder=":hello akira!"
                              @input="propertyChange"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="config.toastConfig.icon" @change="propertyChange">
                        <el-radio label="success"></el-radio>
                        <el-radio label="loading"></el-radio>
                        <el-radio label="none"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="config.toastConfig.position" @change="propertyChange">
                        <el-radio label="top"></el-radio>
                        <el-radio label="center"></el-radio>
                        <el-radio label="bottom"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-switch v-model="config.toastConfig.mask" @change="propertyChange"></el-switch>
                </el-form-item>
                <el-form-item label="">
                    <el-input-number v-model="config.toastConfig.duration" :step="100" size="small"
                                     @change="propertyChange"></el-input-number>
                </el-form-item>
            </div>
            <div class="modal-config" v-show="form.type==2">
                <el-form-item label="">
                    <el-input type="text" v-model="config.modalConfig.title" placeholder=":"
                              @input="propertyChange"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="text" v-model="config.modalConfig.content" placeholder=":hello akira!"
                              @input="propertyChange"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-switch v-model="config.modalConfig.showCancel" @change="propertyChange"></el-switch>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="text" v-model="form.modalConfig.cancelText" placeholder=":"
                              @input="propertyChange"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="text" v-model="config.modalConfig.confirmText" placeholder=":"
                              @input="propertyChange"></el-input>
                </el-form-item>
            </div>
            <div class="custom-config" v-show="config.type==3">
                <el-form-item label="">
                    <el-input type="text" v-model="config.custom.methodName" placeholder=":onClick"
                              @input="propertyChange"></el-input>
                </el-form-item>
            </div>
            <el-form-item class="text-center">
                <el-button type="primary" size="small" round @click="confirm"> </el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "click-event-manager",
        model: {
            prop: 'config',
            event: 'change'
        },
        props: {
            title: {
                type: String,
                default: ''
            },
            config: {
                type: Object
            }
        },
        watch: {
            config: {
                deep: true,
                handler() {
                    this.setConfig()
                }

            }
        },
        created() {
            this.setConfig()
        },
        data() {
            return {}
        },
        methods: {
            confirm() {
                this.$emit('confirm')
            },
            setConfig() {
                if (this.config) {
                    this.form = this.config
                }
            },
            propertyChange() {
                this.$emit('change', this.config)
            }
        }
    }
</script>

<style scoped>
    @import "../../styles/common.scss";
    .click-event-manager {
        max-height: 520px;
        overflow: hidden;
        overflow-y: auto;
    }
</style>
