<template>
    <div class="common-nav-list-property">
        <el-tabs v-model="activeTab" stretch>
            <el-tab-pane label="" name="first">
                <el-form>
                    <el-row :gutter="20">
                        <el-col :span="14">
                            <el-form-item label="">
                                <el-radio-group v-model="property.type" @change="propertyChange">
                                    <el-radio :label="0"></el-radio>
                                    <el-radio :label="1"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="10">
                            <el-form-item label="">
                                <theme-color-picker v-model="property.bgColor" @change="propertyChange"></theme-color-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <div class="list" v-show="property.type===0">
                        <el-row :gutter="20">
                            <el-col :span="12">
                                <el-form-item label="">
                                    <el-switch v-model="property.showArrow" @change="propertyChange"></el-switch>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item label="">
                                    <el-switch v-model="property.cardType" @change="propertyChange"></el-switch>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </div>
                    <div class="grid" v-show="property.type===1">
                        <el-row :gutter="20">
                            <el-col :span="16">
                                <el-form-item label="">
                                    <el-radio-group v-model="property.columns" @change="propertyChange">
                                        <el-radio :label="3">3</el-radio>
                                        <el-radio :label="4">4</el-radio>
                                        <el-radio :label="5">5</el-radio>
                                    </el-radio-group>
                                </el-form-item>
                            </el-col>
                            <el-col :span="8">
                                <el-form-item label="">
                                    <el-switch v-model="property.border" @change="propertyChange"></el-switch>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </div>
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
                <nav-menu-item-component v-for="(menu,index) in property.menus" :key="index"
                                         :menu="menu"
                                         @change="menuItemChange(index,$event)"
                                         @remove="menuItemRemove(index)"
                ></nav-menu-item-component>
                <div class="add-btn text-center">
                    <el-button type="primary" size="small" round icon="el-icon-plus" @click="add"> </el-button>
                </div>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
    import NavMenuItemComponent from "../nav-menu-item-component/nav-menu-item-component";

    export default {
        name: "common-nav-list-property",
        components: {NavMenuItemComponent},
        props: {
            property: {
                type: Object
            }
        },
        data(){
            return {
                activeTab:'first'
            }
        },
        methods: {
            add() {
                const copy = JSON.parse(JSON.stringify(this.property.menus[0]));
                copy.text = '' + (this.property.menus.length + 1);
                this.property.menus.push(copy)
                this.propertyChange()
            },
            menuItemRemove(index) {
                if (this.property.menus.length === 1) {
                    this.$message.error('');
                    return;
                }
                this.property.menus.splice(index, 1);
                this.propertyChange()
            },
            menuItemChange(index, menu) {
                this.$set(this.property.menus, index, menu)
                this.propertyChange()
            },
            propertyChange() {
                this.$emit('propertyChange', {
                    name: this.$options.name,
                    data: this.property
                })
            }
        }
    }
</script>

<style scoped>

</style>
